let store
let curdo = Object.create(null, {}) // 
let undo = [] // 
let redo = [] // 

const isEmptyObject = function (data) {
  if (Object.keys(data).length) {
    return false
  } else {
    return true
  }
}

const pure = function (data) {
  return JSON.parse(JSON.stringify(data))
}

const hasUndo = function () {
  return !!undo.length
}

const hasRedo = function () {
  return !!redo.length
}

const refreshHistoryBtn = function () {
  store.commit('refreshHistoryBtn', {
    hasUndo: hasUndo(),
    hasRedo: hasRedo()
  })
}

const resetHistory = function () {
  redo = []
}
// 
const updateHistory = function () {
  // state
  // setTimeout(() => {
  // 
  if (JSON.stringify(store.state) === JSON.stringify(curdo)) {
    return
  }
  if (!isEmptyObject(curdo)) {
    undo.push(curdo)
  }
  curdo = pure(store.state) // 
  resetHistory() // 
  refreshHistoryBtn()
  // }, 0)
}

const undoHistory = function () {
  if (!hasUndo()) {
    return
  }
  redo.unshift(curdo)
  curdo = undo.pop()
  store.commit('updateHistory', pure(curdo))
  refreshHistoryBtn()
}

const redoHistory = function () {
  if (!hasRedo()) {
    return
  }
  undo.push(curdo)
  curdo = redo.shift()
  store.commit('updateHistory', pure(curdo))
  refreshHistoryBtn()
}

const getCurdo = function () {
  return curdo
}

// store
const history = function (storeObj) {
  store = storeObj
  return {
    update: updateHistory,
    undo: undoHistory,
    redo: redoHistory,
    getCurdo: getCurdo
  }
}

export default history



